import firebase from 'firebase/app'
import commons from '../util/commons'
import collections from './collections'

export default {
    /**
     * 获取当前用户信息
     */
    userInfo() {
        return new Promise(async (resolve, reject) => {
            try {
                let userId = commons.userId()
                let userDoc = await collections.user.doc(userId).get()
                if (userDoc && userDoc.exists) {
                    localStorage.setItem("userInfo", JSON.stringify(userDoc.data()));
                    resolve(userDoc.data());
                } else {
                    resolve(null);
                }
            } catch (error) {
                reject(error)
            }
        })
    },
    teacherInfo() {
        return new Promise(async (resolve, reject) => {
            try {
                let userId = commons.userId()
                let teacherDoc = await collections.teacher.doc(userId).get()
                if (teacherDoc && teacherDoc.exists) {
                    localStorage.setItem('teacherInfo', JSON.stringify(teacherDoc.data()))
                    resolve(teacherDoc.data())
                } else {
                    resolve(null)
                }
            } catch (error) {
                reject(error)
            }
        })
    },
    studioInfo() {
        return new Promise(async (resolve, reject) => {
            try {
                let teacher = await this.teacherInfo()
                if (teacher && teacher.studioId) {
                    let studioDoc = await collections.studio.doc(teacher.studioId).get()
                    if (studioDoc && studioDoc.exists) {
                        localStorage.setItem('studioInfo', JSON.stringify(studioDoc.data()))
                        resolve(studioDoc.data())
                        return
                    }
                }
                resolve(null)
            } catch (error) {
                reject(error)
            }
        })
    }
}